"use client"

import { useState } from "react"
import { useRouter } from "next/navigation"
import Link from "next/link"
import { Button } from "@/components/ui/button"
import { Card, CardContent } from "@/components/ui/card"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs"
import { Textarea } from "@/components/ui/textarea"
import { Slider } from "@/components/ui/slider"
import { Badge } from "@/components/ui/badge"
import { ArrowLeft, Star, Check } from "lucide-react"

// Mock wardrobe items - in real app, would be fetched from API
const WARDROBE_ITEMS = {
  Tops: [
    { id: 1, name: "White Oxford Shirt", image: "/white-oxford-shirt.png" },
    { id: 4, name: "Grey Sweater", image: "/grey-sweater.jpg" },
    { id: 8, name: "Striped T-Shirt", image: "/striped-t-shirt.png" },
  ],
  Bottoms: [
    { id: 3, name: "Black Trousers", image: "/black-trousers.png" },
    { id: 7, name: "Beige Chinos", image: "/beige-chinos.png" },
  ],
  Shoes: [
    { id: 6, name: "White Sneakers", image: "/white-sneakers.png" },
  ],
  Outerwear: [
    { id: 2, name: "Navy Blazer", image: "/navy-blazer.png" },
    { id: 5, name: "Denim Jacket", image: "/classic-denim-jacket.png" },
  ],
  Accessories: [],
}

const FORMALITY_LABELS = ["Home", "Neighborhood", "Outing", "Work", "Formal"]

export default function CreateOutfitPage() {
  const router = useRouter()
  const [selectedItems, setSelectedItems] = useState<Record<string, number>>({})
  const [outfitName, setOutfitName] = useState("")
  const [rating, setRating] = useState(3)
  const [formalityLevel, setFormalityLevel] = useState([3])
  const [memo, setMemo] = useState("")
  const [activeCategory, setActiveCategory] = useState("Tops")

  const toggleItemSelection = (category: string, itemId: number) => {
    setSelectedItems((prev) => {
      if (prev[category] === itemId) {
        const newSelected = { ...prev }
        delete newSelected[category]
        return newSelected
      }
      return { ...prev, [category]: itemId }
    })
  }

  const getSelectedItem = (category: string) => {
    const itemId = selectedItems[category]
    if (!itemId) return null
    return WARDROBE_ITEMS[category as keyof typeof WARDROBE_ITEMS].find((item) => item.id === itemId)
  }

  const handleSave = () => {
    const selectedItemsList = Object.entries(selectedItems).map(([category, itemId]) => ({
      category,
      itemId,
    }))

    // TODO: Implement API call to save outfit
    console.log("Saving outfit:", {
      name: outfitName,
      rating,
      formalityLevel: formalityLevel[0],
      memo,
      items: selectedItemsList,
    })

    router.push("/outfits")
  }

  const hasSelectedItems = Object.keys(selectedItems).length > 0

  return (
    <div className="min-h-screen bg-background">
      <header className="border-b border-border/50 bg-background/80 backdrop-blur-md sticky top-0 z-10">
        <div className="container mx-auto px-6 py-6">
          <div className="flex items-center justify-between">
            <div className="flex items-center gap-4">
              <Button variant="ghost" size="icon" asChild>
                <Link href="/outfits">
                  <ArrowLeft className="w-4 h-4" />
                </Link>
              </Button>
              <h1 className="text-xl font-serif tracking-tight">Create Outfit</h1>
            </div>
            <Button onClick={handleSave} disabled={!hasSelectedItems}>
              Save Outfit
            </Button>
          </div>
        </div>
      </header>

      <main className="container mx-auto px-6 py-12 max-w-6xl">
        <div className="grid lg:grid-cols-2 gap-8">
          {/* Left: Item Selection */}
          <div className="space-y-6">
            <Card className="border border-border/50">
              <CardContent className="p-6">
                <h2 className="text-lg font-serif font-light mb-6">Select Items</h2>
                <Tabs value={activeCategory} onValueChange={setActiveCategory}>
                  <TabsList className="grid grid-cols-4 lg:grid-cols-5 mb-6">
                    <TabsTrigger value="Tops">Tops</TabsTrigger>
                    <TabsTrigger value="Bottoms">Bottoms</TabsTrigger>
                    <TabsTrigger value="Shoes">Shoes</TabsTrigger>
                    <TabsTrigger value="Outerwear">Outer</TabsTrigger>
                    <TabsTrigger value="Accessories" className="hidden lg:block">
                      Access.
                    </TabsTrigger>
                  </TabsList>

                  {Object.entries(WARDROBE_ITEMS).map(([category, items]) => (
                    <TabsContent key={category} value={category} className="mt-0">
                      {items.length === 0 ? (
                        <div className="text-center py-12">
                          <p className="text-muted-foreground text-sm">No items in this category</p>
                          <Button variant="ghost" size="sm" className="mt-4" asChild>
                            <Link href="/wardrobe/add">Add Item</Link>
                          </Button>
                        </div>
                      ) : (
                        <div className="grid grid-cols-2 sm:grid-cols-3 gap-4">
                          {items.map((item) => {
                            const isSelected = selectedItems[category] === item.id
                            return (
                              <div
                                key={item.id}
                                className={`relative cursor-pointer group ${
                                  isSelected ? "ring-2 ring-primary rounded-lg" : ""
                                }`}
                                onClick={() => toggleItemSelection(category, item.id)}
                              >
                                <Card className="border border-border/50 hover:border-primary/30 transition-all">
                                  <CardContent className="p-0">
                                    <div className="aspect-square overflow-hidden rounded-t-lg bg-muted/30">
                                      <img
                                        src={item.image || "/placeholder.svg"}
                                        alt={item.name}
                                        className="w-full h-full object-cover"
                                      />
                                    </div>
                                    <div className="p-3">
                                      <p className="text-sm font-light truncate">{item.name}</p>
                                    </div>
                                  </CardContent>
                                </Card>
                                {isSelected && (
                                  <div className="absolute top-2 right-2 w-8 h-8 rounded-full bg-primary flex items-center justify-center">
                                    <Check className="w-5 h-5 text-primary-foreground" />
                                  </div>
                                )}
                              </div>
                            )
                          })}
                        </div>
                      )}
                    </TabsContent>
                  ))}
                </Tabs>
              </CardContent>
            </Card>
          </div>

          {/* Right: Preview & Details */}
          <div className="space-y-6">
            {/* Preview */}
            <Card className="border border-border/50">
              <CardContent className="p-6">
                <h2 className="text-lg font-serif font-light mb-6">Outfit Preview</h2>
                {!hasSelectedItems ? (
                  <div className="text-center py-12 text-muted-foreground text-sm">
                    Select items from the left to preview your outfit
                  </div>
                ) : (
                  <div className="grid grid-cols-2 gap-4">
                    {Object.keys(WARDROBE_ITEMS).map((category) => {
                      const item = getSelectedItem(category)
                      if (!item) return null
                      return (
                        <div key={category} className="space-y-2">
                          <Badge variant="secondary" className="text-xs font-normal">
                            {category}
                          </Badge>
                          <div className="aspect-square overflow-hidden rounded-lg bg-muted/30 border border-border/50">
                            <img
                              src={item.image || "/placeholder.svg"}
                              alt={item.name}
                              className="w-full h-full object-cover"
                            />
                          </div>
                          <p className="text-xs font-light text-center">{item.name}</p>
                        </div>
                      )
                    })}
                  </div>
                )}
              </CardContent>
            </Card>

            {/* Rating & Details */}
            <Card className="border border-border/50">
              <CardContent className="p-6 space-y-6">
                <h2 className="text-lg font-serif font-light">Outfit Details</h2>

                {/* Outfit Name */}
                <div className="space-y-3">
                  <Label className="text-sm font-light text-muted-foreground uppercase tracking-wider">
                    Outfit Name (Optional)
                  </Label>
                  <Input
                    placeholder="e.g., Office Casual"
                    className="border-border/50"
                    value={outfitName}
                    onChange={(e) => setOutfitName(e.target.value)}
                  />
                </div>

                {/* Rating */}
                <div className="space-y-3">
                  <Label className="text-sm font-light text-muted-foreground uppercase tracking-wider">
                    How much do you like this outfit?
                  </Label>
                  <div className="flex items-center gap-2">
                    {[1, 2, 3, 4, 5].map((value) => (
                      <button
                        key={value}
                        onClick={() => setRating(value)}
                        className="transition-colors"
                        type="button"
                      >
                        <Star
                          className={`w-8 h-8 ${
                            value <= rating ? "fill-primary text-primary" : "text-muted-foreground/30"
                          }`}
                        />
                      </button>
                    ))}
                  </div>
                </div>

                {/* Formality Level */}
                <div className="space-y-4">
                  <div className="flex items-center justify-between">
                    <Label className="text-sm font-light text-muted-foreground uppercase tracking-wider">
                      Formality Level
                    </Label>
                    <Badge variant="outline" className="border-border/50">
                      {formalityLevel[0]} - {FORMALITY_LABELS[formalityLevel[0] - 1]}
                    </Badge>
                  </div>
                  <Slider
                    value={formalityLevel}
                    onValueChange={setFormalityLevel}
                    min={1}
                    max={5}
                    step={1}
                    className="py-4"
                  />
                  <div className="flex justify-between text-xs text-muted-foreground">
                    <span>1 - Home</span>
                    <span>5 - Formal</span>
                  </div>
                </div>

                {/* Memo */}
                <div className="space-y-3">
                  <Label className="text-sm font-light text-muted-foreground uppercase tracking-wider">
                    Memo (Optional)
                  </Label>
                  <Textarea
                    placeholder="e.g., For important meetings"
                    className="border-border/50 min-h-24"
                    value={memo}
                    onChange={(e) => setMemo(e.target.value)}
                  />
                </div>
              </CardContent>
            </Card>
          </div>
        </div>
      </main>
    </div>
  )
}
